import React from 'react'
import { Button } from 'reactstrap'


// CSS
import './about.css'

export default function About() {
  return (
    <div className="aboutMainContainer" id="about">

      <div className='aboutSubContainer'>


        <div className='aboutInformation'>
          <span className="aboutInfoTitle">Our Story</span>
          <p className="aboutInfoRegText">Starting with just a laptop and a cell phone, reaching out to businesses and seeing what we can do to help was our mission. We knew our skillset, and we loved putting in energy to help others, so we figured we would bridge the gap between merchant services and technology.</p>
          <p className="aboutInfoRegText">We decided to build the tools we have today to better help our friends in the business world.</p>
          <Button outline color="primary">Contact Us</Button>

        </div>

        <div className='aboutImage'>
          <img src="https://cdn.clipart.email/7cb0abb8ea6b2b46716d27f2fb289115_apartment-flat-hdb-transparent-png-clipart-free-download-ywd_512-512.png"></img>
        </div>

      </div>
    </div>
  )
}
