import React, { Component } from 'react'

// CSS
import './footer.css'

export default class Footer extends Component {
  render() {
    return (
      <div className="footerMainContainer">

        <div className='footerOfficeInfo'>
          <span className="footerOfficeTitle">Office</span>
          <span className="footerOfficeAddr1">12345 Washington Avenue</span>
          <span className="footerOfficeAddr2">Murrieta, CA 92562</span>
        </div>

        <div className='footerContactInfo'>
          <span className="footerContactTitle">Contact</span>
          <span className="footerContactPhone">(951) 555-1234</span>
          <span className="footerContactEmail">info@merch-tools.com</span>
        </div>

        <div className='footerDetailsInfo'>
          <span className="footerDetailsTitle">Details</span>
          <span className="footerDetailsPhone">Privacy Policy</span>
          <span className="footerDetailsEmail">2020 Merch-Tools, LLC</span>
        </div>

        <div className='footerSocialMedia'>
          <span className="footerSocialPhone">Twitter</span>
          <span className="footerSocialEmail">Facebook</span>
        </div>

      </div>
    )
  }
}
