import React from 'react'
import { Button } from 'reactstrap'

// CSS
import './ourClients.css'

export default function OurClients() {
  return (
    <div className="ourClientsMainContainer" id="clients">

      <div className="ourClientsSubContainer">
        <div className='ourClientsInformation'>
          <span className="ourClientsInfoTitle">Happy Customers is our Goal</span>
          <p className="ourClientsInfoRegText">Our knowledgebase and performance is our strategy. We are ready to help, figure out what you need to get the job done.</p>
          <Button outline color="primary">Client List</Button>
        </div>

        <div className='ourClientsImage'>
          <img src="http://getdrawings.com/free-icon/user-flat-icon-59.png"></img>
        </div>
      </div>

    </div>
  )
}
